@import url(https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700&family=Nunito+Sans:opsz,wght@6..12,200;6..12,400;6..12,700&display=swap);
.indicator-class {
  display: none;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter ", sans-serif;
}

::-webkit-scrollbar {
  width: 8px;
  height: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  /* box-shadow: inset 0 0 3px grey; */
  border-radius: 2px;
  width: 1px;
}
.iconImage {
  display: flex;
  align-items: center;
  justify-content: center;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(99, 149, 153, 0.7);
  border-radius: 2px;
  width: 1px;
  height: 1px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #1b4b66;
}
.icon {
  color: red;
}

.tab {
  border-radius: 8px;
  background-color: #f1f1f1;
  color: #626262;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  padding: 6px 18px;
}
.spinner {
  display: block;
  width: 100px;
  height: 100px;
  border-top: 10px solid #ff8c4b;
  border-bottom: 10px solid #b00020;
  border-right: 10px solid #1b4b66;
  border-left: 10px solid #639599;
  border-radius: 50%;
  margin: auto;
  /* border-top: 6px solid #1b4b66; */
  -webkit-animation: spin 0.8s linear infinite;
          animation: spin 0.8s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    scale: 0.9;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    scale: 1.1;
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    scale: 0.9;
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    scale: 1.1;
  }
}
.custom-tab-link {
  text-decoration: none;
  color: black;
}

